<template>
  <div
   class="advantage-container"
   ref="advantage_container"
   :style="isMobile ? {'background-position-y' : position.top +'px'} : ''"
  >
    <div class="container">
      <div class="content">
        <div class="content-title">
          <div class="zh-title theme-color-one theme-font-three">{{ title.zh }}</div>
          <div class="en-title theme-color-two theme-font-three">{{ title.en }}</div>
        </div>
        <div class="content-list">
          <div class="item-container-one">
            <AdvantageItem
            v-for="(item, index) in filteredItem(0, 5)"
            :key="index"
            :dataObject="item"
            class="theme-color-one"
            ></AdvantageItem>
          </div>
          <div class="item-container-two">
            <AdvantageItem
            v-for="(item, index) in filteredItem(5, 9)"
            :key="index"
            :dataObject="item"
            class="theme-color-one"
            ></AdvantageItem>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      title: {
        zh: '产品优势',
        en: 'ADVANTAGE',
      },
      items: [
        {
          number: '01',
          main: '--IPA智能算法',
          sub: '使用IPA智能并行算法，数据精准保持稳定的利润率。',
        },
        {
          number: '02',
          main: '--商户独立',
          sub: '领先的行业技术，商户分离，互不影响。',
        },
        {
          number: '03',
          main: '--AI智能系统',
          sub: 'VIP决策系统，商户可自由选择应对策略，智能大客甄别。',
        },
        {
          number: '04',
          main: '--CDN加速域名防劫持',
          sub: '加快访问速度，防劫持保障站点安全稳定运营。',
        },
        {
          number: '05',
          main: '--支持全平台',
          sub: '全面支持PC版、HTML5手机版、APP手机版，方便玩家随时随地体验',
        },
        {
          number: '06',
          main: '--多级代理',
          sub: '低门槛，高收益，轻松进阶，轻松赚钱。',
        },
        {
          number: '07',
          main: '--最稳定的管理系统',
          sub:
            '集成玩家管理、资金管理、游戏管理，运营管理、统计报表、多种支付、內容管理、系统设置等模块。',
        },
        {
          number: '08',
          main: '--技术支持',
          sub: '数十位高端技术人才24小时为您解决问题。',
        },
        {
          number: '09',
          main: '--快速转站',
          sub: '老平台转到AA更便捷，无需担心影响玩家游戏',
        },
      ],
      itemFlags: {
        colOne: 0,
        colTwo: 5,
      },
      windowSize: {
        width: 0,
        height: 0,
      },
      position: {
        top: 0,
      },
      navHeight: 65,
      isMobile: false,
      tempHeightNum: 0,
    };
  },
  components: {
    AdvantageItem: () => import('../components/AdvantageItem.vue'),
  },
  methods: {
    filteredItem(start, end) {
      return this.items.slice(start, end);
    },
    handleResize() {
      this.windowSize.width = document.documentElement.clientWidth;
      if (this.windowSize.width < '576' || this.windowSize.width < 576) {
        this.isMobile = true;
      } else {
        this.isMobile = false;
      }
    },
    handleScroll() {
      const pos = this.$refs.advantage_container.getBoundingClientRect();
      const percentHeight = pos.height < 1000 ? 0.45 : 0.3;
      const limitHeight = (pos.height - (pos.height * percentHeight)) / 2;
      if (pos.top <= this.navHeight) {
        if ((pos.top * -1) < limitHeight) {
          this.position.top = (this.tempHeightNum - pos.top) + this.navHeight;
        }
      } else {
        this.position.top = pos.top;
        this.tempHeightNum = this.position.top;
      }
    },
  },
  mounted() {
    window.addEventListener('resize', this.handleResize, true);
    window.addEventListener('scroll', this.handleScroll, false);
    this.handleResize();
  },
};
</script>
